    opacity: 1;
  }
  .modal-content {
    background-color: transparent;
    box-shadow: 0 0 0;
  }
  .modal-backdrop.in {
    opacity: 0.45;
  }
  .preload-image {
    display: none;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.15), 0 0 1px 1px rgba(0, 0, 0, 0.1);
